import React from 'react';
import AddFishForm from './AddFishForm';
import firebase, { auth, googleProvider, facebookProvider, twitterProvider, githubProvider } from '../firebase';

class Inventory extends React.Component {

    constructor() {
        super();

        this.renderInventory = this.renderInventory.bind(this);
        this.renderLogin = this.renderLogin.bind(this);
        this.authenticate = this.authenticate.bind(this);
        this.logout = this.logout.bind(this);
        this.handleChange = this.handleChange.bind(this);

        this.state = {
            uid: null,
            owner: null
        }
    }

    componentDidMount() {
        // every single time the app loads to see 
        // if the user was already signed in last time
        // they visit the app
        auth.onAuthStateChanged((user) => {
            if (user) {
                this.authHandler(null, { user });
            }
        });
    }

    handleChange(e, key) {
        const fish = this.props.fishes[key];

        const updatedFish = {
            ...fish,
            [e.target.name]: e.target.value
        }

        this.props.updateFish(key, updatedFish);
    }

    authenticate(provider) {
        console.log(`Trying to log in with ${provider}`);
        // base.authWithOAuthPopup(provider, this.authHandler);

        switch (provider) {
            case 'github':
                provider = githubProvider;
                break;
            case 'google':
                provider = googleProvider;
                break;
            case 'twitter':
                provider = twitterProvider;
                break;
            case 'facebook':
                provider = facebookProvider;
                break;
            default:
                provider = null;
        }

        if (provider) 
            // signInWithPopup has a promise API
            auth.signInWithPopup(provider)
                .then((result) => {
                    console.log(user);
                    const user = result.user;
                    this.setState({
                        user
                    });
                });
    }

    logout() {
        // base.unauth();
        auth.signOut()
            .then(() => {
                this.setState({
                    uid: null
                });
            });
    }

    authHandler(err, authData) {
        console.log(authData);
        if (err) {
            console.error(err);
            return;
        }

        // grab the store info
        const storeRef = firebase.database().ref(this.props.storeId);

        // query the firebase once for the store data
        storeRef.once('value', (snapshot) => {
            const data = snapshot.val() || {};

            // claim it as our own if there is no owner already
            if (!data.owner) {
                storeRef.set({
                    owner: authData.user.uid
                });
            }

            this.setState({
                uid: authData.user.uid,
                owner: data.owner || authData.user.uid
            })
        });
    }

    renderLogin() {
        return (
            <nav className="login">
                <h2>Inventory</h2>
                <p>Sign in to manage your store's inventory</p>
                <button className="github" onClick={() => this.authenticate('google')}>Log In with Google</button>
                <button className="github" onClick={() => this.authenticate('github')}>Log In with Github</button>
                {/* <button className="facebook" onClick={() => this.authenticate('facebook')}>Log In with Facebook</button> */}
                <button className="twitter" onClick={() => this.authenticate('twitter')}>Log In with Twitter</button>
            </nav>
        )
    }

    renderInventory(key) {
        const fish = this.props.fishes[key];
        return (
            <div className="fish-edit" key={key}>
                <input type="text" name="name" value={fish.name} placeholder="Fish Name" onChange={(e) => this.handleChange(e, key)} />
                <input type="text" name="price" value={fish.price} placeholder="Fish Price" onChange={(e) => this.handleChange(e, key)} />

                <select type="text" name="status" value={fish.status} placeholder="Fish Status" onChange={(e) => this.handleChange(e, key)}>
                    <option value="available">Fresh!</option>
                    <option value="unavailable">Sold Out!</option>
                </select>

                <textarea type="text" name="desc" value={fish.desc} placeholder="Fish Desc" onChange={(e) => this.handleChange(e, key)}></textarea>
                <input type="text" name="image" value={fish.image} placeholder="Fish Image" onChange={(e) => this.handleChange(e, key)} />
                <button onClick={() => this.props.removeFish(key)}>Remove Fish</button>
            </div>
        )
    }

    render() {
        const logout = <div><button onClick={this.logout}>Log Out!</button><br/></div>;

        // check if they are no logged in at all
        if (!this.state.uid) {
            return <div>{this.renderLogin()}</div>
        }

        // check if they are the owner of the current store
        if (this.state.uid !== this.state.owner) {
            return (
                <div>
                    <p>Sorry you aren't the owner of this store!</p>
                    {logout}
                </div>
            )
        }

        return (
            <div>
                <h2>Inventory</h2>
                {logout}
                {Object.keys(this.props.fishes).map(this.renderInventory)}
                <AddFishForm addFish={this.props.addFish} />
                <button onClick={this.props.setSamples}>Load Sample Fishes</button>
            </div>
        )
    }
}

export default Inventory;


