import React from 'react';
import Header from './Header';
import Order from './Order';
import Inventory from './Inventory';
import Fish from './Fish';
import SampleFishes from '../sample-fishes';
import firebase from '../firebase';

class App extends React.Component {
    constructor() {
        super();
        this.state = {
            fishes: {},
            order: {}
        }

        this.addFish = this.addFish.bind(this);
        this.setSamples = this.setSamples.bind(this);
        this.addToOrder = this.addToOrder.bind(this);
        this.updateFish = this.updateFish.bind(this);
        this.removeFish = this.removeFish.bind(this);
    }

    componentWillMount() {
        this.ref = firebase.database().ref(`${this.props.match.params.storeId}/fishes`);
        this.ref.on('value', (snapshot) => {
            let fishes = snapshot.val();

            if (fishes) {
                this.setState({
                    fishes
                });
            }
        });

        // check if there is any order in localstorage
        const localStorageRef = localStorage.getItem(`order-${this.props.match.params.storeId}`);

        if (localStorageRef) {
            // update our App's Component order state
            this.setState({
                order: JSON.parse(localStorageRef)
            });
        }
    }

    componentWillUnmount() {
        this.ref.off();
        // base.removeBinding(this.ref);
    }

    componentWillUpdate(nextProps, nextState) {
        const { match: { params } } = this.props;
        localStorage.setItem(`order-${params.storeId}`, JSON.stringify(this.state.order));
    }

    setSamples() {
        this.ref.set(SampleFishes);
        // this.setState({ fishes: SampleFishes });
    }

    addFish(fish) {
        // add in our new fish
        const timestamp = Date.now();

        // save to firebase
        this.ref.child(`fish-${timestamp}`).set(fish);

        // don't need anymore becase we handle in 
        // firebase value event
        /* // update our state
        const fishes = {...this.state.fishes};

        // add in our new fish
        const timestamp = Date.now();

        fishes[`fish-${timestamp}`] = fish;

        // set state
        this.setState({ fishes: fishes }); */
    }

    addToOrder(key) {
        // take a copy of our state
        const order = { ...this.state.order }

        // update or add the new number of fish ordered
        order[key] = order[key] + 1 || 1;

        // update our state
        this.setState({ order });
    }

    updateFish(key, updateFish) {
        // update to firebase
        this.ref.child(key).update(updateFish);

        /* const fishes = { ...this.state.fishes };
        fishes[key] = updateFish;
        this.setState({ fishes }); */
    }

    removeFish(key) {
        // delete to firebase
        this.ref.child(key).remove();

        // const fishes = { ...this.state.fishes };
        // fishes[key] = null;
        // this.setState({ fishes });
    }

    render() {
        return (
            <div className="catch-of-the-day">
                <div className="menu">
                    <Header tagline="Fresh Seafood Market" />
                    <ul>
                        {
                            Object.keys(this.state.fishes)
                                .map(key => <Fish key={key} index={key} details={this.state.fishes[key]} addToOrder={this.addToOrder} />)
                        }
                    </ul>
                </div>
                <Order fishes={this.state.fishes} order={this.state.order} />
                <Inventory fishes={this.state.fishes}
                    addFish={this.addFish}
                    setSamples={this.setSamples}
                    updateFish={this.updateFish}
                    removeFish={this.removeFish}
                    storeId={this.props.match.params.storeId} />
            </div>
        );
    }
}

export default App;


